.form input {
    width: 100%;
    outline: none;
    border: none;
    background-color: whitesmoke;
    padding: 10px 20px;
    border-radius: 30px;
}

.showpasswordbutton {
    margin-left: -40px;
    cursor: pointer;
}

.cal-containter {
    background-color: whitesmoke;
    height: 100%;
    /* width: 100%; */
    /* color: white; */
    text-align: center;
    padding: 5px;
    border-radius: 10px;
}

.cal-mon,
.cal-day,
.cal-year,
.cal-date {
    font-size: 13px;
    font-weight: 700;
    margin-left: 4px;
}

.cal-body {
    display: flex;
    padding: 2px 5px;
    border: 1px solid white;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
}

.cal-date {
    font-size: 15px;
    font-weight: 900;
    color: red;
}
.timer-element {
    display: flex;
    flex-direction: column;
    margin-left: 4px;
}

.element-label {
    font-size: 10px;
    /* text-align: right; */
}
.section{
    padding: 40px 0px;
    
}
.textarea {
    height: 40vh;
    resize: none;
    overflow-y: auto;
}

input[type="date"],
input[type="time"] {
    font-size: larger;
    border: none;
    margin: 0px;
    padding: 0px;
    /* background: transparent; */
    font-size: 27px;
    outline: none;
}

input::-webkit-calendar-picker-indicator {
    cursor: pointer;
}

input::-webkit-datetime-edit,
input::-webkit-inner-spin-button,
input::-webkit-clear-button {
    display: none;
}

.img {
    height: 45px;
    width: 45px;
    border-radius: 50%;
    border: 2px solid red;
    display: flex;
    align-items: center;
    justify-content: center;
    background: black;
}

.img img {
    padding: 1px;
    height: inherit;
    width: inherit;
    object-fit: contain;
    border-radius: 50%;
}
.ql-toolbar{
    
    display: flex;
    justify-content: center;

}

.ql-editor {
    height: 180px;
}
.usericon {
    height: 30px;
    width: 30px;
    border-radius: 50%;
    border: 2px solid red;
    display: flex;
    align-items: center;
    justify-content: center;
    background: black;
}

.usericon img {
    padding: 1px;
    height: inherit;
    width: inherit;
    object-fit: contain;
    border-radius: 50%;
}

.task-body {
    max-height: 100px;
}
.openSetting {
    background-color: transparent;
    border: none;
    display: grid;
    place-items: center;
    position: relative;

}

.openSetting:hover {
    -webkit-transform: rotateZ(90deg);
            transform: rotateZ(90deg);
}

.setting {
    border-radius: 20px;
    position: fixed;
    background-color: white;
    right: 20px;
    /* top: 10px; */
    z-index: 1;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: none;
}
.header {
    width: 100%;
    font-size: 32px;
    font-weight: 600;
    background-color: white;
    resize: none;
    border: none;
}
